import React, {useState , useEffect} from 'react';
import {Avatar , Button, Menu, Typography , } from 'antd'
import {HomeOutlined , FundOutlined, BulbOutlined, MenuOutlined, MoneyCollectOutlined} from '@ant-design/icons'
import icon from '../images/icon.png'
import { Link } from 'react-router-dom';
const Navbar = () => {
    const [activeMenu, setActiveMenu] = useState(true);
  const [screenSize, setScreenSize] = useState(undefined);

  useEffect(() => {
    const handleResize = () => setScreenSize(window.innerWidth);

    window.addEventListener('resize', handleResize);

    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    if (screenSize <= 800) {
      setActiveMenu(false);
    } else {
      setActiveMenu(true);
    }
  }, [screenSize]);
    return (
        <div className="nav-container">
            <div className="logo-container">
            
                <div className="logo">
                    <Link to="/"><Avatar src={icon} /> &nbsp;Crypto Trcker</Link>
                </div>
                <Button className="menu-control-container" onClick={() => setActiveMenu(!activeMenu)}>
                    <MenuOutlined />
                </Button>
            </div>
                {activeMenu && <Menu theme="dark">
                    <Menu.Item icon={<HomeOutlined />}>
                        <Link to="/">Home</Link>
                    </Menu.Item>
                    <Menu.Item icon={<FundOutlined />}>
                        <Link to="/cryptocurrency">Cryptocurrency</Link>
                    </Menu.Item>
                    <Menu.Item icon={<MoneyCollectOutlined />}>
                        <Link to="/exchanges">Exchanges</Link>
                    </Menu.Item>
                    <Menu.Item icon={<BulbOutlined />}>
                        <Link to="/news">News</Link>
                    </Menu.Item>
                </Menu>}
            
        </div>
    );
};

export default Navbar;